.smart-city_box.building_summary-box .building-details .building-nearby-box ul li.police-station::before {
  background-image: url("https://static.iviva.com/images/Udhayimages/mda-building-images/police-station.png");
}
.smart-city_box.building_summary-box .building-details .building-nearby-box ul li.mosques::before {
  background-image: url("https://static.iviva.com/images/Udhayimages/mda-building-images/mosques.png");
}
.smart-city_box.building_summary-box .building-details .building-nearby-box ul li.restaurants::before {
