  height: 100%;
}
.smart-city_box.building_layout-box .smart-city-content .slideshow-container {
  display: flex;
  width: 100%;
  height: 80%;
}
.smart-city_box.building_layout-box .slideshow-container {
  margin-left: 0em;
  margin-top: 0;
  position: relative;
  background: #fff;
  padding: 10px;
  display: inline-block;
  width: 100%;
  height: 80%;
}
.smart-city_box.building_layout-box .slideshow-container .myslides1, .smart-city_box.building_layout-box .slideshow-container .myslides2, .smart-city_box.building_layout-box .slideshow-container .myslides3, .smart-city_box.building_layout-box .slideshow-container .myslides4 {
  width: 100%;
  height: 100%;
}
.smart-city_box.building_layout-box .slideshow-container img {
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  height: 100%;
