.smart-city_box.building_layout-box #ViewsMenu .model-items .menu-item .centering-container .blue-button.enabled a {
  color: white;
  text-decoration: none;
}
.smart-city_box.building_layout-box #ViewsMenu .model-items .menu-item .centering-container .blue-button {
